



















































  import vueagreement from '../../components/agreement.vue'
  
  const COUNTSECEND = 60
  
	export default{
    components: {
      vueagreement
    },
		data(){
			return{
				phone: '', // 手机号码
        code: '', // 验证码
        agree: true, // 是否同意协议
        hasSendCode: false,
        isClickCode: false,
        sending: false,
        countNum: COUNTSECEND,
        countTimer: null,
        pop: false,
        submiting: false,
        agreeData: {}
			}
		},
    computed: {
    	config() {
    		return this.$store.state.config
    	}
    },
    onShow() {
    	// 是否处于验证码获取倒计时状态
      if (uni.getStorageSync('agentCodeStamp')) {
        let oldtime = uni.getStorageSync('agentCodeStamp')
        let nowtime = new Date().getTime()
        let cha = parseInt((nowtime - parseInt(oldtime)) / 1000)
        if (cha < COUNTSECEND) {
          this.sending = true
          this.countNum = COUNTSECEND - cha
          this.countTimer = setInterval(() => {
            this.countNum --
            if (this.countNum <= 0) {
              clearInterval(this.countTimer)
              this.countNum = COUNTSECEND
              this.sending = false
              this.isClickCode = false
            }
          }, 1000)
          this.hasSendCode = true
        }
      }
    },
		methods:{
      checkAgree() {
        this.agree = !this.agree
      },
      showAgree() {
        this.agreeData = {
          show: true
        }
      },
      sendCode() {
        if (this.isClickCode) return
        if (!this.$utils.validatePhone(this.phone)) {
          uni.showToast({
          	title: '手机号码格式错误',
            icon: 'none'
          })
        } else {
          this.isClickCode = true
          if (!this.hasSendCode) this.hasSendCode = true
          this.$http({
            url: this.$apis.phone,
            method: 'POST',
            data: {
              type: 4,
              mobile: this.phone
            },
            cb: (err, data) => {
              if (!err && data.success) {
                this.sending = true
                this.countTimer = setInterval(() => {
                  this.countNum --
                  if (this.countNum <= 0) {
                    clearInterval(this.countTimer)
                    this.countNum = COUNTSECEND
                    this.sending = false
                    this.isClickCode = false
                  }
                }, 1000)
                // 保存发送时间
                uni.setStorageSync('agentCodeStamp', (new Date().getTime()).toString())
              } else {
                uni.showToast({
                	title: data.message,
                  icon: 'none'
                })
                this.sending = false
                this.isClickCode = false
              }
            }
          })
        }
      },
      confirm() {
        if (!this.$utils.validatePhone(this.phone)) {
          uni.showToast({
          	title: '请输入正确的手机号码',
            icon: 'none'
          })
        } else if (!this.hasSendCode) {
          uni.showToast({
          	title: '验证码已失效，请重新发送',
            icon: 'none'
          })
        } else if (this.code.length <= 0) {
          uni.showToast({
          	title: '请输入验证码',
            icon: 'none'
          })
        } else if (!this.agree) {
          uni.showToast({
          	title: '请阅读并勾选《' + this.config.system_nick_name + '用户协议》',
            icon: 'none'
          })
        } else {
          this.pop = true
        }
      },
      submit() {
        if (this.submiting) return
        let _this = this
        _this.submiting = true
        this.$http({
          url: this.$apis.levelUp,
          method: 'POST',
          data: {
            mobile: _this.phone,
            verify_code: _this.code
          },
          cb: (err, data) => {
            if (!err && data.success === true) {
              uni.removeStorageSync('agentCodeStamp')
              uni.setStorageSync('agentInvitePop', true)
              this.$getInfo(b => {
                if (b) {
                  uni.switchTab({
                  	url: '/pages/shop/index'
                  })
                }
              })
            } else {
              uni.showModal({
                title: '升级失败',
              	content: data ? (data.message ? data.message : '升级失败，请稍后再试') : '升级失败，请稍后再试',
                showCancel: false,
                confirmText: '确定'
              })
            }
            _this.submiting = false
            _this.pop = false
          }
        })
      }
		}
	}
